@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@700&display=swap);
* {
  box-sizing: border-box;
  font-family: "Noto Sans KR", sans-serif;
}

/* Nav css */

.Nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.nav-logo {
  /* 로고 사진 넣어보고 조절하기*/
  width: 100px;
  height: 50px;
  margin-left: 5px;
}

.nav-item {
  display: flex;
  margin-right: 5%;
  width: 300px;
  justify-content: space-around;
  line-height: 50px;
}

a {
  color: black;
  text-decoration: none;
}

/* Nav css - End*/

/* Main css - Start*/
.Main {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 30rem;
  background-color: #c4c4c4;
  background-image: url(/heycard/static/media/Main.601cc72e.png);
  margin-top: 170px;
  justify-content: center;
  align-items: center;
}

.main-text {
  position: absolute;
  text-align: center;
  top: 37%;
}

.main-text p {
  color: whitesmoke;
}

.main-btn {
  position: absolute;
  width: 200px;
  height: 50px;
  background-color: #a259ff;
  text-align: center;
  line-height: 50px;
  bottom: 45%;
  border-radius: 10px;
}
/* Main css - End*/

/* Signin css - Start*/
.Main-sign {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 400px;
  margin-top: 200px;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.sign-logo {
  position: absolute;
  top: 20%;
}

.sign-text {
  position: absolute;
  text-align: center;
  top: 40%;
  line-height: 100%;
}

.sign-btn {
  position: absolute;
  width: 300px;
  height: 50px;
  background-color: #fee500;
  text-align: center;
  line-height: 50px;
  bottom: 44%;
  border-style: none;
  border-radius: 10px;
  background-image: url(data:image/png;base64,);
}

.sign-btn2 {
  position: absolute;
  bottom: 40%;
}

/* Signin css - End*/

.Sidebaritem {
  margin-top: 10px;
  width: 100px;
  height: 30px;
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.6);
}
.modal button {
  outline: none;
  cursor: pointer;
  border: 0;
}
.modal > section {
  width: 90%;
  max-width: 450px;
  margin: 0 auto;
  border-radius: 0.3rem;
  background-color: #fff;
  /* 팝업이 열릴때 스르륵 열리는 효과 */
  animation: modal-show 0.3s;
  overflow: hidden;
}
.modal > section > header {
  position: relative;
  padding: 16px 64px 16px 16px;
  background-color: #f1f1f1;
  font-weight: 700;
}
.modal > section > header button {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 30px;
  font-size: 21px;
  font-weight: 700;
  text-align: center;
  color: #999;
  background-color: transparent;
}
.modal > section > main {
  padding: 16px;
  border-bottom: 1px solid #dee2e6;
  border-top: 1px solid #dee2e6;
}
.modal > section > footer {
  padding: 12px 16px;
  text-align: right;
}
.modal > section > footer button {
  padding: 6px 12px;
  color: #fff;
  background-color: #6c757d;
  border-radius: 5px;
  font-size: 13px;
}
.modal.openModal {
  display: flex;
  align-items: center;
  /* 팝업이 열릴때 스르륵 열리는 효과 */
  animation: modal-bg-show 0.3s;
}
@keyframes modal-show {
  from {
    opacity: 0;
    margin-top: -50px;
  }
  to {
    opacity: 1;
    margin-top: 0;
  }
}
@keyframes modal-bg-show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

